.container {
  display: flex;
  flex-direction: column;
}
.title {
  height: 100%;
}
.main {
  flex: 1;
}
.footer {
  height: auto;
}

.ant-layout-header,
.ant-layout-footer {
  background: radial-gradient(
    circle farthest-corner at 50.3% 44.5%,
    rgba(116, 147, 179, 1) 0%,
    rgba(62, 83, 104, 1) 100.2%
  );
  color: #fff;
  text-align: center;
  padding: 0 50px;
}

.ant-layout-content {
  background: radial-gradient(
    circle farthest-corner at 50.3% 44.5%,
    rgba(116, 147, 179, 1) 0%,
    rgba(62, 83, 104, 1) 100.2%
  );
  color: #fff;
  min-height: 120px;
  line-height: 120px;
  text-align: center;
}

.ant-row {
  width: 100vw;
}
