.contentWrapper {
  overflow-x: hidden;
  height: 100%;
  min-height: 80vh;
}

.mapWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.gutter-row {
  margin: 0 auto;
}

.d3-tip {
  line-height: 1.6;
  font-weight: bold;
  padding: 8px;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 4px;
  width: 150px;
  height: 120px;
  font-size: 0.8rem;
}

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: rgba(0, 0, 0, 0.8);
  content: '\25BC';
  position: absolute;
  text-align: center;
}

/* Style northward tooltips differently */
.d3-tip.n:after {
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
}
